// src\router\index.js:
import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
    meta: {
      title: "Home - Portfolio",
      description: "Welcome to my portfolio website",
    },
  },
  {
    path: "/about",
    name: "about",
    component: () => import("../views/AboutView.vue"),
    meta: {
      title: "About Me - Portfolio",
      description: "Learn more about my background and experience",
    },
  },
  {
    path: "/services",
    name: "services",
    component: () => import("../views/ServicesView.vue"),
    meta: {
      title: "Services - Portfolio",
      description: "Explore my web development services",
    },
  },
  {
    path: "/codecanyon-doctor",
    name: "codecanyon-doctor",
    component: () => import("../views/CodecanyonDoctorView.vue"),
    meta: {
      title: "Codecanyon Doctor | Laravel Script Fixing & Customization",
      description:
        "Professional Laravel Codecanyon script fixing, customization, and support. Bug fixes, payment gateway integration, deployment, and more.",
      keywords:
        "codecanyon doctor, laravel script fix, codecanyon customization, laravel bug fix, payment gateway integration, SSLCommerz, bKash, Nagad",
    },
  },
  {
    path: "/expertise",
    name: "expertise",
    component: () => import("../views/ExpertiseView.vue"),
    meta: {
      title: "Expertise - Portfolio",
      description: "Discover my technical skills and expertise",
    },
  },
  {
    path: "/projects",
    name: "projects",
    component: () => import("../views/ProjectsView.vue"),
    meta: {
      title: "Projects - Portfolio",
      description: "View my portfolio of completed projects",
    },
  },
  {
    path: "/blogs",
    name: "blogs",
    component: () => import("../views/BlogsView.vue"),
    meta: {
      title: "Blog - Portfolio",
      description: "Read my latest articles and insights",
    },
  },
  {
    path: "/contact",
    name: "contact",
    component: () => import("../views/ContactView.vue"),
    meta: {
      title: "Contact Me - Portfolio",
      description: "Get in touch for collaborations and inquiries",
    },
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to) {
    document.body.style.overflow = "";
    if (to.hash) {
      return {
        el: to.hash,
        behavior: "smooth",
      };
    }
    return { top: 0, behavior: "smooth" };
  },
});

router.beforeEach((to, from, next) => {
  document.body.style.overflow = "";

  if (to.meta.title) {
    document.title = to.meta.title;
  }

  if (to.meta.description) {
    const metaDescription = document.querySelector('meta[name="description"]');
    if (metaDescription) {
      metaDescription.setAttribute("content", to.meta.description);
    }
  }

  if (to.meta.keywords) {
    const metaKeywords = document.querySelector('meta[name="keywords"]');
    if (metaKeywords) {
      metaKeywords.setAttribute("content", to.meta.keywords);
    }
  }

  //  REMOVE OLD SCHEMA IF EXISTS
  const oldSchema = document.getElementById("codecanyon-schema");
  if (oldSchema) oldSchema.remove();

  //  ADD JSON-LD SCHEMA
  const schema = {
    "@context": "https://schema.org",
    "@type": "ProfessionalService",
    name: "Codecanyon Doctor",
    url: window.location.href,
    areaServed: "Worldwide",
    serviceType: "Laravel Codecanyon Customization",
    description:
      "Professional Laravel Codecanyon script fixing, customization, bug fixing, payment gateway integration and deployment services.",
    provider: {
      "@type": "Person",
      name: "Tawshiqul Islam",
    },
    sameAs: [
      "https://github.com/tawshiqulislam",
      "https://www.linkedin.com/in/tawshiqul-islam-rafi/",
      "https://www.facebook.com/tawshiqulislam.rafi.3",
    ],
  };

  const script = document.createElement("script");
  script.type = "application/ld+json";
  script.id = "codecanyon-schema";
  script.text = JSON.stringify(schema);

  document.head.appendChild(script);

  next();
});

export default router;
