<template>
  <header class="navbar-wrapper" :class="{ 'navbar-scrolled': hasScrolled }">
    <div class="navbar-container">
      <!-- Logo Section -->
      <router-link to="/" class="nav-logo" @click="toggleBack">
        <div class="logo-container" :class="{ 'back-mode': isAnyNavActive }">
          <transition name="logo-flip">
            <img
              v-if="!isAnyNavActive"
              :src="logoSrc"
              alt="Logo"
              class="logo-image"
            />
            <div v-else class="back-button">
              <i class="bi bi-arrow-left-circle-fill h1 text-warning"
                >&emsp;Home</i
              >
            </div>
          </transition>
        </div>
      </router-link>

      <!-- Desktop Menu -->
      <nav class="desktop-menu" :class="{ 'menu-hidden': isMobileMenuOpen }">
        <ul class="nav-list">
          <li v-for="(item, index) in navItems" :key="index" class="nav-item">
            <!-- Regular nav items without dropdown -->
            <router-link
              v-if="!item.dropdown"
              :to="item.path"
              class="nav-link"
              :class="{ 'nav-active': toggleNav[index] === 1 }"
              @click="toggleNavItem(index)"
            >
              <div class="nav-icon-wrapper">
                <i :class="'bi ' + item.icon"></i>
                <div
                  class="nav-pulse"
                  :class="{ 'pulse-active': toggleNav[index] === 1 }"
                ></div>
              </div>
              <span class="nav-text">{{ item.name }}</span>
              <div class="nav-indicator"></div>
            </router-link>

            <!-- Services dropdown - CLICK VERSION -->
            <div
              v-else
              class="nav-dropdown-wrapper"
              v-click-outside="() => closeDropdown(index)"
            >
              <div
                class="nav-link dropdown-trigger"
                :class="{
                  'nav-active':
                    toggleNav[index] === 1 || activeDropdown === index,
                }"
                @click="toggleDropdown(index)"
              >
                <div class="nav-icon-wrapper">
                  <i :class="'bi ' + item.icon"></i>
                  <div
                    class="nav-pulse"
                    :class="{ 'pulse-active': toggleNav[index] === 1 }"
                  ></div>
                </div>
                <span class="nav-text">{{ item.name }}</span>
                <i
                  class="bi bi-chevron-down dropdown-icon"
                  :class="{ rotated: activeDropdown === index }"
                ></i>
                <div class="nav-indicator"></div>
              </div>

              <!-- Dropdown Menu - Click to open -->
              <transition name="dropdown-slide">
                <div v-if="activeDropdown === index" class="dropdown-menu">
                  <router-link
                    v-for="(subItem, subIndex) in item.dropdown"
                    :key="subIndex"
                    :to="subItem.path"
                    class="dropdown-item"
                    @click="handleDropdownItemClick(index)"
                  >
                    <i :class="'bi ' + subItem.icon"></i>
                    <div class="dropdown-item-content">
                      <span class="dropdown-item-title">{{
                        subItem.name
                      }}</span>
                      <span class="dropdown-item-desc">{{
                        subItem.description
                      }}</span>
                    </div>
                  </router-link>
                </div>
              </transition>
            </div>
          </li>
        </ul>
      </nav>

      <!-- Mobile Menu Toggle Button -->
      <button
        class="mobile-toggle"
        @click="toggleMobileMenu"
        aria-label="Toggle mobile menu"
      >
        <div class="hamburger" :class="{ 'is-active': isMobileMenuOpen }">
          <span>&#8737;</span>
        </div>
      </button>
    </div>

    <!-- Mobile Menu Overlay -->
    <transition name="mobile-menu">
      <div
        v-if="isMobileMenuOpen"
        class="mobile-menu-overlay"
        @click="handleOverlayClick"
      >
        <div class="mobile-menu-container" ref="mobileMenuRef">
          <ul class="mobile-nav-list">
            <li
              v-for="(item, index) in navItems"
              :key="index"
              class="mobile-nav-item"
              :style="{ animationDelay: index * 0.1 + 's' }"
            >
              <!-- Regular mobile nav items -->
              <router-link
                v-if="!item.dropdown"
                :to="item.path"
                class="mobile-nav-link"
                :class="{ 'mobile-nav-active': toggleNav[index] === 1 }"
                @click="mobileNavClick(index)"
              >
                <i :class="'bi ' + item.icon"></i>
                <span>{{ item.name }}</span>
                <div class="mobile-link-highlight"></div>
              </router-link>

              <!-- Mobile dropdown -->
              <div v-else class="mobile-dropdown">
                <div
                  class="mobile-nav-link mobile-dropdown-trigger"
                  :class="{
                    'mobile-nav-active': toggleNav[index] === 1,
                    'dropdown-open': mobileDropdownOpen[index],
                  }"
                  @click="toggleMobileDropdown(index)"
                >
                  <i :class="'bi ' + item.icon"></i>
                  <span>{{ item.name }}</span>
                  <i
                    class="bi bi-chevron-down mobile-dropdown-icon"
                    :class="{ rotated: mobileDropdownOpen[index] }"
                  ></i>
                  <div class="mobile-link-highlight"></div>
                </div>

                <!-- Mobile dropdown items -->
                <div
                  v-show="mobileDropdownOpen[index]"
                  class="mobile-dropdown-content"
                  :class="{ 'is-open': mobileDropdownOpen[index] }"
                >
                  <router-link
                    v-for="(subItem, subIndex) in item.dropdown"
                    :key="subIndex"
                    :to="subItem.path"
                    class="mobile-dropdown-item"
                    @click="mobileNavClick(index)"
                  >
                    <i :class="'bi ' + subItem.icon"></i>
                    <span>{{ subItem.name }}</span>
                  </router-link>
                </div>
              </div>
            </li>
          </ul>
          <div class="mobile-menu-footer">
