</template>

<script>
import logo from "./assets/image/logo.png";

export default {
  data() {
    return {
      logoSrc: logo,
      toggleNav: [0, 0, 0, 0, 0, 0],
      isMobileMenuOpen: false,
      hasScrolled: false,
      activeDropdown: null,
      mobileDropdownOpen: {},
      navItems: [
        { name: "About", icon: "bi-person-circle", path: "/about" },
        {
          name: "Services",
          icon: "bi-box2-heart-fill",
          path: "/services",
          dropdown: [
            {
              name: "Codecanyon Doctor",
              icon: "bi-wrench-adjustable-circle",
              path: "/codecanyon-doctor",
              description: "Laravel Script Fixing & Customization",
            },
            {
              name: "Other Services",
              icon: "bi-grid-3x3-gap-fill",
              path: "/services",
              description: "Web Development & More",
            },
          ],
        },
        { name: "Expertise", icon: "bi-briefcase-fill", path: "/expertise" },
        { name: "Projects", icon: "bi-bar-chart-steps", path: "/projects" },
        { name: "Blog", icon: "bi-journal-text", path: "/blogs" },
        { name: "Contact", icon: "bi-question-circle-fill", path: "/contact" },
      ],
    };
  },
  computed: {
    isAnyNavActive() {
      return this.toggleNav.includes(1);
    },
  },
  directives: {
    "click-outside": {
      mounted(el, binding) {
        el.clickOutsideEvent = function (event) {
          if (!(el === event.target || el.contains(event.target))) {
            binding.value();
          }
        };
        document.addEventListener("click", el.clickOutsideEvent);
      },
      unmounted(el) {
        document.removeEventListener("click", el.clickOutsideEvent);
      },
    },
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    this.checkCurrentRoute();
    window.addEventListener("resize", this.checkScreenSize);
    this.checkScreenSize();
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
    window.removeEventListener("resize", this.checkScreenSize);
  },
  methods: {
    checkCurrentRoute() {
      const currentPath = this.$route.path;
      const index = this.navItems.findIndex((item) => {
        if (item.dropdown) {
          return item.dropdown.some((subItem) => subItem.path === currentPath);
        }
        return item.path === currentPath;
      });
      if (index !== -1) {
        this.toggleNavItem(index);
      }
    },
    toggleNavItem(index) {
      const newToggleNav = [0, 0, 0, 0, 0, 0];
      newToggleNav[index] = 1;
      this.toggleNav = newToggleNav;
      this.activeDropdown = null;
      if (this.isMobileMenuOpen) {
        setTimeout(() => {
          this.isMobileMenuOpen = false;
          this.mobileDropdownOpen = {};
        }, 300);
      }
    },
    toggleBack() {
      this.toggleNav = [0, 0, 0, 0, 0, 0];
      this.activeDropdown = null;
    },
    toggleDropdown(index) {
      if (this.activeDropdown === index) {
        this.activeDropdown = null;
      } else {
        this.activeDropdown = index;
      }
    },
    closeDropdown(index) {
      if (this.activeDropdown === index) {
        this.activeDropdown = null;
      }
    },
    handleDropdownItemClick(index) {
      this.toggleNavItem(index);
      this.activeDropdown = null;
    },
    toggleMobileDropdown(index) {
      this.mobileDropdownOpen = {
        ...this.mobileDropdownOpen,
        [index]: !this.mobileDropdownOpen[index],
      };
    },
    handleOverlayClick(event) {
      if (event.target === event.currentTarget) {
        this.closeMobileMenu();
      }
    },
    closeMobileMenu() {
      this.isMobileMenuOpen = false;
      this.mobileDropdownOpen = {};
      document.body.style.overflow = "";
    },
    toggleMobileMenu() {
      this.isMobileMenuOpen = !this.isMobileMenuOpen;
      document.body.style.overflow = this.isMobileMenuOpen ? "hidden" : "";
    },
    mobileNavClick(index) {
      this.toggleNavItem(index);
    },
    handleScroll() {
      this.hasScrolled = window.scrollY > 20;
    },
    checkScreenSize() {
      if (window.innerWidth > 768) {
        this.isMobileMenuOpen = false;
        this.mobileDropdownOpen = {};
        document.body.style.overflow = "";
      }
    },
  },
};
</script>

